<script>
import {
  getData,
  opthionList,
  CompanySwitchClear,
  tableResize
} from "common/utils/public_function/commonUtils";
import getAreaList from "common/components/public/getAreaList";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "instorageOtheninstoragedetailOtheninstoragedetail",
  data() {
    return {
      defaultArea: [0], //默认的区域id
      timeValue: [], // 时间
      loading:false,
      sumLoading: false, // 合计加载
      TableData: {
        tableData: [],
        allList: {},
        total: 10
      },
      tableHeight: 600
    };
  },
  components: {
    getAreaList //区域列表
  },
  methods: {
    // 接口 获取列表
    portGetList(params) {
      let _params = {
        params: params,
        TableData: this.TableData,
        API: "getOtherStockDetailList", //接口名
        vm: this //传递的this
      };
      getData(_params);
      this.portGetListSum(); // 请求合计
    },
    // 接口 获取列表合计
    portGetListSum() {
      this.sumLoading = true; // 开启加载
      this.$axMethod
        .getOtherStockDetailListSum(this.searchParams.params)
        .then(({ data }) => {
          this.sumLoading = false; // 关闭加载
          if (data.code == 200) {
            this.$set(this.TableData, "allList", data.data);
          }
        })
        .catch(error => {
          this.sumLoading = false; // 关闭加载
        });
    },
    // 清除字段
    clickClear(obj, keyArr) {
      keyArr.forEach(key => {
        obj[key] = "";
      });
    },
    allocationInventoryListchange(val) {
      if (val != "") {
        this.params.allocation_name = this.allocationInventoryList.filter(
          obj => obj.allocation_id === val
        )[0].allocation_name;
      }
    },
    departmentChange(val) {
      if (val != "") {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
      this.$set(this.params, "buyer_id", "");
    },
    receivablesModeIdChange(val) {
      if (val != "") {
        this.params.receivables_mode_name = this.receivablesList.filter(
          obj => obj.receivables_mode_id === val
        )[0].receivables_mode_name;
      }
    },
    clearAllocation(val) {
      this.allocationInventoryList = [];
      if (val != "") {
        this.params.warehouse_name = this.warehouseList.filter(
          obj => obj.warehouse_id === val
        )[0].warehouse_name;
      }
      this.$set(this.params, "allocation_id", null);
      this.InventoryInvok();
    },
    exportOrder(scheduleType) {
      // 导出其它出库明细
      let s = Object.assign(this.params, {
        export_type: scheduleType
      });
      this.$axMethod.exportOtherStockDetailUrl(this.params).then(response => {
        if (response.data.code == 200) {
          window.location.href = response.data.data.url;
        }
      });
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "页小计";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (column.property == "total_price") {
            sums[index] = parseFloat(sums[index])
              .toFixed(2)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else if (column.property == "real_num") {
            sums[index] = sums[index];
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });
      return sums;
    },
    companyChange(val) {
      if (val != "") {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
      CompanySwitchClear(this, this.params);
    },
    changeDefaultArea(addressId) {
      this.params.cityList = addressId.join(",");
      this.params.province_id = addressId[0];
      this.params.city_id = addressId[1];
      this.params.area_id = addressId[2];
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.defaultArea = [0];
      this.$set(this, "params", {
        auditor_status: 0
      });
      this.allocationInventoryList = [];
      this.timeValue = [];
      this.$set(this, "searchParams", {
        params: this.params
      });
      this.onSearch();
    },
    onSearch() {
      //查询
      if (this.timeValue) {
        this.params.business_start_time = this.timeValue[0];
        this.params.business_end_time = this.timeValue[1];
      } else {
        this.params.business_start_time = this.params.business_end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      this.portGetList(this.searchParams.params);
    },
    moduleHandleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      this.portGetList(this.searchParams.params);
    },
    moduleHandleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      this.portGetList(this.searchParams.params);
    }
  },
  mounted() {
    this.$set(this, "searchParams", {
      params: this.params //1默认带的参数
    });

    //取消单据右侧滚动条
    window.onresize = tableResize.bind(this);

    const elTable1 = this.$refs.table1.$el.getElementsByClassName(
      "el-table__body-wrapper"
    )[0];
    const elTable2 = this.$refs.table_sum;
    elTable1.addEventListener("scroll", function() {
      elTable2.scrollLeft = this.scrollLeft;
    });
  },
  activated() {
    this.portGetList(this.searchParams.params);
  }
};
</script>
